@import "../../sass/variables";

.container {
  max-width: 1440px;
  padding: 0 15px;
  margin: auto;
  font-size: 1.5rem;
  @media (max-width: 1120px) {
   max-width: 1090px;
  }
  @media (max-width: 768px) {
    width: 100%;
  }
  @media (max-width: 414px) {
    width: 100%;
  }
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 768px) {
    flex-direction: column;
  }
}

.col {
  width: 30%;
  margin: 0 16px 44px;
  @media (max-width: 768px) {
    width: 100%;
  }
}



.info {
  font-family: 'Nekst';
  font-style: normal;
  font-weight: 900;
  font-size: 54px;
  line-height: 90%;
  color: $color_dark;
  @media (max-width: 414px) {
    font-size: 22px;
    line-height: 91%;
  }
}


